import { Box, AspectRatio, Button, Icon, Link, Text, WrapItem, ExternalLinkIcon} from '@chakra-ui/react'
import { Image } from '@chakra-ui/image'
import { GoMarkGithub } from "react-icons/go";
import {CgWebsite} from 'react-icons/cg'


function Github({props}) {
    if (props.github) {
        return(
            <Link href={props.github} isExternal>
                <Button  ml='5px' px='3px' leftIcon = {<Icon as={GoMarkGithub}/>} color='white' fontSize='12px' size='xs' variant='outline'>
                        View Source
                </Button>
            </Link>
        )
    }
    else if (props.website) {
        return(
            <Link href={props.website} isExternal>
                <Button ml='5px' px='3px' leftIcon = {<Icon as={CgWebsite}/>} color='white' fontSize='12px' size='xs' variant='outline'>
                        View Website
                </Button>
            </Link>
        )
    }
}

function ProjectCard({props}) {
    

    return(
            <Box align='left' p='10px' w='200px' boxShadow='2xl' maxH='330px' borderRadius='10px' overflow='hidden'>
                <Box>              
                    <Box>
                        <AspectRatio ratio ={4/3}>
                            <Image objectFit='cover' borderRadius='10px' p='0px' src={props.image} alt='AI algorithm' />
                        </AspectRatio>
                    </Box>
                </Box>
                <Box isTruncated maxH='48px' color='white' fontFamily='Courier New' fontWeight='semibold' p='3px'>
                {props.title}
                </Box>
                <Box overflow='auto' h='110px' fontSize='12' p='3px'>
                    <Text> {props.desc} </Text>
                </Box>
                <Github props={props} />
            </Box>
    )
}

export default ProjectCard